const colorsDict = {
  white: "",
  black: "",
  red: "",
  blue: "",
  green: "",
  yellow: "",
  purple: "",
  pink: "",
};

export default function colorFormat(color) {
  const normColor = color.trim().toLowerCase();
  if (Object.hasOwn(colorsDict, normColor)) {
    return colorsDict[normColor];
  }
  return color;
}
